<template>
  <div>
    <!-- 评论区 -->
    <div class="comment-item">
      <div class="user-info">
        <div class="header">
          <img :src="$axios.defaults.baseURL+data.user.head_img" alt />
        </div>
        <div class="text">
          <h4>{{data.user.nickname}}</h4>
          <div class="content">{{data.content}}</div>
          <div>
            <span class="time">{{moment(data.create_date).fromNow()}}</span>
            <div class="apply" @click="myclick(data)">回复</div>
          </div>
        </div>
      </div>
      <commentitem v-if="data.parent" :data="data.parent" @reply="myclick"></commentitem>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  // 声明当前的组件名，在当前组件里面，name的值可以用于调用组件自己
  name: "commentitem",
  props: ["data"],
  data() {
    return {
      moment
    };
  },
  methods: {
    myclick(data) {
      this.$emit("reply", this.data);
    }
  }
};
</script>

