<template>
  <div class="container">
    <!-- 头部导航 -->
    <navbar class="navbar">
      <div class="back" slot="back">
        <i class="iconfont iconjiantou2" @click="$router.back()"></i>
      </div>
      <h4 slot="title" class="title">评论</h4>
    </navbar>
    <!-- 评论区 -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :immediate-check="false"
    >
      <div class="comment" v-for="(item,index) in comment" :key="index">
        <div class="user-info">
          <div class="header">
            <img :src="$axios.defaults.baseURL+item.user.head_img" alt />
          </div>
          <div class="text">
            <h4>{{item.user.nickname}}</h4>
            <div class="content">{{item.content}}</div>
            <div>
              <span class="time">{{moment(item.create_date).fromNow()}}</span>
              <div class="apply" @click="handelreply(item,$event)">回复</div>
            </div>
          </div>
        </div>
        <!-- 评论列表组件 -->
        <commentitem v-if="item.parent" :data="item.parent" @reply="handelreply"></commentitem>
      </div>
    </van-list>
    <footer>
      <!-- 高度自适应
      对于 textarea，可以通过autosize属性设置高度自适应-->
      <van-field
        v-model.trim="message"
        rows="1"
        autosize
        type="textarea"
        :placeholder="reply.user? '回复：@'+reply.user.nickname:'请输入留言'"
        @focus="mfocus"
        @blur="mblur"
        :class="{active:myfocus}"
        @keydown.enter="sendmessage"
        :autofocus="myfocus"
        ref="textarea"
      />
      <div class="button">
        <button @click="sendmessage">发送</button>
      </div>
    </footer>
  </div>
</template>

<script>
import navbar from "@/components/navbar";
import moment from "moment";
import commentitem from "@/components/comment-item";
// 转换国际语言，zh-CN就是中文（en 就是默认的英文）
moment.locale("zh-CN");
export default {
  data() {
    return {
      token: "",
      comment: "",
      moment,
      pid: "",
      // 是否正在加载中
      loading: false,
      // 数据是否加载完毕
      finished: false,
      // 请求的页数
      pageIndex: 1,
      // 请求的条数
      pageSize: 5,
      // 输入的文本内容
      message: "",
      myfocus: false,
      // 评论对象
      reply: {}
    };
  },

  // 获取评论列表
  mounted() {
    const { token } = JSON.parse(localStorage.getItem("user_info"));
    this.token = token;
    const id = this.$route.params.id;
    this.pid = id;
    this.getcomment();
  },
  methods: {
    onLoad() {
      this.getcomment();
    },
    mfocus() {
      this.myfocus = true;
    },
    mblur() {
      this.myfocus = false;
      if (this.comment.content == "") {
        this.message = "";
      }
    },
    getcomment() {
      this.$axios({
        url: `/post_comment/${this.pid}`,
        methods: "get",
        params: {
          pageIndex: this.pageIndex,
          pageSize: this.pageSize
        }
      })
        .then(res => {
          const { data } = res.data;
          this.comment = [...data, ...this.comment];
          this.pageIndex++;
          // 加载状态结束
          this.loading = false;
          // 当评论的条数小于每页的条数时，证明可以是加载完
          if (data.length < this.pageSize) {
            this.finished = true;
          }
          // console.log(this.comment);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 发布评论
    sendmessage(value) {
      console.log(value);
      if (this.message === "") return;
      // 评论接口的参数
      const data = {
        content: this.message
      };
      // 如果reply有值，说明当前是一条回复的评论
      if (this.reply.id) {
        // parent_id 就是回复的评论的id
        data.parent_id = this.reply.id;
      }
      this.$axios({
        url: "/post_comment/" + this.pid,
        method: "POST",
        headers: {
          Authorization: this.token
        },
        data
      }).then(res => {
        // 重新请求列表数据
        this.comment = []; // 必须要清空，如果不清空会合并之前的评论
        this.pageIndex = 1;
        this.getcomment();
        // 清空回复的数据
        this.reply = {};
        console.log(res);
        console.log(data);
      });
    },
    // 回复评论
    handelreply(item, event) {
      this.reply = item;
      this.myfocus = true;
      this.message = "";
      //通过ref注册的实例获取textarea 输入框获得焦点
      this.$refs.textarea.focus();
    }
  },
  components: {
    navbar,
    commentitem
  }
};
</script>

